*{
  background-color: #659dbd;

}
.game-area {
  position: relative;
  margin: auto;
  margin-top: 15vh;
  width: 600px;
  height: 600px;
  border: 2px solid #000;
}

.snake-dot {
  position: absolute;
  width: 2%;
  height: 2%;
  background-color: rgb(11, 243, 11);
  border: 1px solid #fff;
  z-index: 2;
  border-radius: 20%;
}

.snake-food {
  position: absolute;
  width: 2%;
  height: 2%;
  background: red;
  border: 1px solid #fff;
  z-index: 1;
  border-radius: 20%;

}
.img{
  border-radius: 100%; 
   width: 7vh;
   height: 7vh; 
 }
.footicon
{
  margin-left:3rem;
  border-radius: 40%;
  color: white;
}
.header{
  /* background-color: #bccbde; */
  color:white;
  font-size: 2rem;
  font-weight: bolder;
}